/******************************************************************
  - MIXINS -
******************************************************************/
.button-block {
  margin-top: 1rem;
  margin-bottom: 1rem;
}
.button-block .button {
  font-size: 1rem;
}

@media print, screen and (min-width: 64em) {
  .button-block .button.small {
    font-size: 0.875rem;
  }
  .button-block .button.medium {
    font-size: 1rem;
  }
  .button-block .button.large {
    font-size: 1.625rem;
  }
  .button-block .button.xlarge {
    font-size: 2rem;
  }
}